.App {
  text-align: center;
  height: 100vh;
  width: 100vw;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  background-color:#E75153;
  -webkit-user-select:none;
     -moz-user-select:none;
      -ms-user-select:none;
          user-select:none;
}

#drum-machine{
  height: 350px;
  width: 600px;
  background-color: #58B89D;
  border: 8px solid #4F3A4B;
  border-radius: 10px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
  -ms-flex-align: center;
      align-items: center;
}

#drum-pad-div{
  width: 300px;
  height: 300px;
  padding: 10px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: grid;
  grid-template-columns: auto auto auto;
  grid-template-rows: auto auto auto;
  grid-column-gap: 10px;
  grid-row-gap: 10px;
  grid-template-areas: 
    "Q W E" 
    "A S D"
    "Z X C";
}

#control-div{
  width: 200px;
  height: 300px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
  -ms-flex-align: center;
      align-items: center;
}

.drum-pad{
  border: 1px solid #6f9990;
  border-radius: 5px;
  background-color: #459785;
  -webkit-box-shadow: 2px 2px 2px 2px;
          box-shadow: 2px 2px 2px 2px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  cursor: pointer;
}

.drum-pad:active{
  background-color: #ffa500;
}

.down{
  background-color: #ffa500;
}

.switch {
  position: relative;
  display: inline-block;
  width: 38px;
  height: 20px;
}

.switch input {display:none;}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #d3d3d3;
  -webkit-transition: .4s;
  -o-transition: .4s;
  transition: .4s;
  border-radius: 20px;
}

.slider:before {
  position: absolute;
  content: "";
  height: 18px;
  width: 18px;
  left: 1px;
  bottom: 1px;
  background-color: white;
  -webkit-transition: .4s;
  -o-transition: .4s;
  transition: .4s;
  border-radius: 50%;
}

input:checked + .slider {
  background-color: #832f30;
}

input:focus + .slider {
  -webkit-box-shadow: 0 0 1px #832f30;
          box-shadow: 0 0 1px #832f30;
}

input:checked + .slider:before {
  -webkit-transform: translateX(18px);
      -ms-transform: translateX(18px);
          transform: translateX(18px);
}

#display{
  width: 150px;
  height: 50px;
  background-color: #d3d3d3;
  opacity: 0.7;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack:center;
      justify-content:center;
  -ms-flex-align: center;
      align-items: center;
}

.rslider {
  -webkit-appearance: none;
  width: 100%;
  height: 15px;
  border-radius: 5px;
  background: #d3d3d3;
  outline: none;
  opacity: 0.7;
  -webkit-transition: .2s;
  -webkit-transition: opacity .2s;
  -o-transition: opacity .2s;
  transition: opacity .2s;
}

.rslider:hover {
  opacity: 1;
}

.rslider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background: #832f30;
  cursor: pointer;
}

.rslider::-moz-range-thumb {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background: #832f30;
  cursor: pointer;
}


